import { Component, Host, h, Prop, Method, Watch, Event, EventEmitter } from '@stencil/core';
const backIcon = '/img/back-icon.png';

export interface BillImageDetailData {
  id: string;
  billImages: string[];
  selectedImageUrl?: string;
}

@Component({
  tag: 'bill-image-detail',
  styleUrl: 'bill-image-detail.css',
  shadow: true,
})
export class BillImageDetail {
  @Prop({ mutable: true, reflect: true }) visible: boolean = false;
  @Prop() billData: BillImageDetailData;
  @Event() closeDetail: EventEmitter<void>;

  @Watch('visible')
  visibleChanged(newValue: boolean) {
    if (newValue) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
  }

  /**
   * Open the bill image detail drawer
   */
  @Method()
  async open() {
    this.visible = true;
  }

  /**
   * Close the bill image detail drawer
   */
  @Method()
  async close() {
    this.visible = false;
    this.closeDetail.emit();
  }

  private handleClose() {
    this.close();
  }

  disconnectedCallback() {
    document.body.style.overflow = '';
  }

  render() {
    if (!this.visible || !this.billData) {
      return null;
    }

    return (
      <Host class={this.visible ? 'visible' : ''}>
        <div class="bill-image-detail-drawer">
          <div class="bill-image-detail-header">
            <div class="header-title">
              <img class="back-icon" src={backIcon} onClick={() => this.handleClose()} />
              <span class="back-title">店家账单图片</span>
            </div>
            <div class="header-close" onClick={() => this.handleClose()}>
              <span>×</span>
            </div>
          </div>

          <div class="bill-image-detail-content">
            {this.billData.selectedImageUrl ? (
              <div class="bill-image-con">
                <img class="bill-image" src={this.billData.selectedImageUrl} alt="Selected Bill Image" />
              </div>
            ) : Array.isArray(this.billData.billImages) && this.billData.billImages.length > 0 ? (
              this.billData.billImages.map((image, index) => (
                <div class="bill-image-con" key={index}>
                  <img class="bill-image" src={image} alt={`Bill Image ${index + 1}`} />
                </div>
              ))
            ) : (
              <div class="no-images">暂无账单图片</div>
            )}
          </div>
        </div>
      </Host>
    );
  }
}
