:host {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1001;
}

:host(.visible) {
  display: block;
}

.bill-image-detail-drawer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 80%;
  max-width: 900px;
  background: #fff;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.15);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 1001;
  display: flex;
  flex-direction: column;
}

:host(.visible) .bill-image-detail-drawer {
  transform: translateX(0);
}

.bill-image-detail-header {
  padding: 24px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  display: flex;
  align-items: center;
}

.header-title .back-icon {
  width: 6px;
  height: 12px;
  cursor: pointer;
}

.header-title .back-title {
  color: #36373d;
  font-weight: 500;
  margin-left: 8px;
  font-size: 16px;
  line-height: 22px;
}

.header-close {
  cursor: pointer;
  font-size: 20px;
  color: #00000073;
}

.bill-image-detail-content {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
}

.bill-image-con {
  box-sizing: border-box;
  margin-bottom: 24px;
  padding: 24px 53px;
  background: #fafcff;
  border-radius: 16px;
}

.bill-image {
  width: 100%;
  border-radius: 14px;
  box-shadow: 0px 5px 21px 0px #003e8f0d;
}

.no-images {
  text-align: center;
  color: #999;
  padding: 40px 0;
  font-size: 14px;
} 